.contactsHero__title {
  font-size: 36px;
  line-height: 48px;
  font-weight: bold;
  margin-left: -2px;
}
.contactsHero__title .big {
  margin-left: -1px;
  font-size: 89px;
  color: #006dfe;
  margin-top: 25px;
}
@media (max-width: 1199px) {
  .contactsHero__title .big {
    font-size: 60px;
    margin-top: 63px;
  }
}
.contactsHero__image {
  margin-left: -81px;
  transform: translateX(50px);
}
.contactsHero__image2 img {
  transform: translateX(30px) rotate(15deg);
}
@media (max-width: 1199px) {
  .contactsHero__image2 img {
    margin-left: 65px;
    bottom: -125px;
  }
}
@media (max-width: 767px) {
  .contactsHero__image2 img {
    width: 200px;
    height: 240px;
    margin-left: 20px;
    bottom: -100px;
  }
}
.contacts {
  background: url("images/noise.jpg");
  padding: 115px 0 120px;
}
@media (max-width: 1199px) {
  .contacts {
    padding: 98px 0 68px;
  }
}
@media (max-width: 1199px) {
  .contacts {
    padding: 109px 0 74px;
  }
}
.contacts .c {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
@media (max-width: 1199px) {
  .contacts .c {
    flex-direction: column;
  }
}
.contacts__left {
  width: 680px;
  height: 525px;
  border: 20px solid #fff;
  padding: 10px;
  flex-shrink: 0;
  margin-left: 10px;
}
@media (max-width: 1199px) {
  .contacts__left {
    width: 100%;
    margin-left: 0;
  }
}
.contacts__border-outer {
  height: 100%;
  border: 10px solid #fff;
  padding: 10px;
  position: relative;
}
.contacts__border-outer:before,
.contacts__border-outer:after {
  content: '';
  display: block;
  position: absolute;
}
.contacts__border-outer:before {
  background: url("images/sprite-corner1.png");
  top: -10px;
  right: -10px;
}
.contacts__border-outer:after {
  background: url("images/sprite-corner2.png");
  bottom: -10px;
  left: -10px;
}
.contacts__border-inner {
  height: 100%;
  background: #ddd;
  position: relative;
}
.contacts__border-inner:before {
  content: '';
  z-index: -1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 45px 40px rgba(29,29,29,0.05);
}
.contacts__map {
  height: 100%;
  width: 100%;
  z-index: 1;
}
.contacts__right {
  font-size: 18px;
  line-height: 30px;
  padding-left: 77px;
}
@media (max-width: 1199px) {
  .contacts__right {
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .contacts__right {
    text-align: center;
  }
}
@media (max-width: 1199px) {
  .contacts__cols {
    display: flex;
    margin-top: 43px;
  }
}
@media (max-width: 767px) {
  .contacts__cols {
    flex-direction: column;
  }
}
.contacts__col {
  flex: 1;
}
.contacts ul {
  margin-bottom: 42px;
}
@media (max-width: 1199px) {
  .contacts ul {
    margin-bottom: 0;
  }
}
.contacts li {
  margin-bottom: 18px;
  position: relative;
  padding-left: 25px;
}
.contacts li:before {
  content: '';
  display: block;
  position: absolute;
  width: 8px;
  height: 8px;
  background: #006dfe;
  left: 0;
  top: 11px;
}
@media (max-width: 767px) {
  .contacts li:before {
    display: none;
  }
}
.contacts .last {
  margin-bottom: 8px;
}
@media (max-width: 1199px) {
  .contacts .last {
    display: none;
  }
}
@media (max-width: 767px) {
  .contacts .last {
    display: block;
  }
}
.contacts .last li {
  margin-bottom: 0;
}
.contacts .sm {
  display: none;
}
@media (max-width: 1199px) {
  .contacts .sm {
    display: block;
  }
}
@media (max-width: 767px) {
  .contacts .sm {
    display: none;
  }
}
@media (max-width: 1199px) {
  .contacts__button {
    text-align: center;
    margin-top: 25px;
  }
}
